.title {
  margin: 0;
  font-style: normal;
  font-weight: 700;
  font-size: 46px;
  line-height: 130%;
  color: var(--color-black);
  @include mobile {
    font-size: 29px;
  }
}
.sud-title {
  margin: 0;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 150%;
  color: var(--color-gray);
}
.block-text {
  margin: 0;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 160%;
}
.form-input {
  padding: 11px 16px;
  border: 1px solid #d7dadd;
  border-radius: 4px;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
  color: var(--color-gray);
}
.form-caption {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
  color: var(--color-mako);
}
.page-title {
  margin: 0;
  max-width: 600px;
  font-style: normal;
  font-weight: 700;
  font-size: 72px;
  line-height: 130%;
  letter-spacing: 1px;
  color: var(--color-black);
  @include small-tablet {
    font-size: 55px;
  }
}
.page-under__title {
  margin: 0;
  font-weight: 400;
  font-size: 20px;
  line-height: 150%;
  color: var(--color-gray);
  @include small-tablet {
    font-size: 18px;
  }
}
