.header {
  padding: 25px 0;
  @include tablet {
    display: none;
  }
  &__container {
    display: flex;
    align-items: center;
    gap: 60px;
  }
  &--bg {
    background-color: var(--color-white);
  }
  &__content {
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__social {
    display: flex;
    align-items: center;
    gap: 88px;
  }

  &__social-item {
    display: flex;
    flex-direction: column;
    background-repeat: no-repeat;
    background-position: left center;
    background-size: 40px 40px;
    padding-left: 60px;
    transition: color 0.4s;
    &:hover {
      .header__social-descr,
      .header__social-text {
        color: var(--color-primary);
      }
    }
  }

  &__social-descr {
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 150%;
    color: var(--color-mako);
    transition: color 0.4s;
  }

  &__social-text {
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    color: var(--color-black);
    transition: color 0.4s;
  }

  &__social--mail {
    background-image: url(../img/chat.svg);
  }
  &__social--tel {
    background-image: url(../img/phone.svg);
  }
  &--second {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 5;
  }
}
