.footer {
  padding: 80px 0;
  background-color: var(--color-black);
  &__top {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 45px;
    margin-bottom: 50px;
  }

  &__top-left {
    max-width: 495px;
    @include tablet {
      max-width: none;
    }
  }

  &__left-content {
    display: flex;
    align-items: center;
    gap: 40px 90px;
    margin-bottom: 25px;
    flex-wrap: wrap;
  }

  &__left-descr {
    margin: 0;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: var(--color-white);
  }

  &__top-right {
    max-width: 495px;
  }

  &__right-title {
    margin: 0;
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 150%;
    color: var(--color-white);
    margin-bottom: 25px;
  }

  &__right-text {
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 150%;
    color: var(--color-white);
    opacity: 0.6;
  }
  &__center {
    display: flex;
    flex-wrap: wrap;
    gap: 50px 125px;
  }
  &__center-title {
    margin: 0;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;
    color: var(--color-white);
    margin-bottom: 15px;
  }

  &__list {
    max-width: 500px;
  }

  &__link {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    color: var(--color-gray);
  }
}
.footer-adress {
  max-width: 500px;
  &__content {
    display: flex;
    flex-direction: column;
    gap: 5px;
  }

  &__item {
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    color: var(--color-gray);
  }

  &__name {
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    color: var(--color-white);
    margin-right: 5px;
  }
}
.footer-social {
  display: flex;
  gap: 18px;
  &__item {
    svg {
      fill: var(--color-white);
      width: 20px;
      height: 20px;
    }
  }
}
.footer-form {
  display: flex;
  align-items: center;
  &__label {
    flex: 1 1 auto;
  }
  &__input {
    width: 100%;
    padding: 11px 16px;
    background: rgba(255, 255, 255, 0.12);
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 4px 0 0 4px;
  }
  &__btn {
    background-color: var(--color-primary);
    padding: 0 25px;
    font-weight: 700;
    font-size: 14px;
    line-height: 43px;
    color: var(--color-white);
  }
}
