.partners {
  padding: 50px 0 80px 0;
  @include tablet {
    padding: 30px 0 35px 0;
  }
  &__title {
    text-align: center;
    margin-bottom: 32px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 40px;
    @include small-tablet {
      grid-template-columns: repeat(3, 1fr);
      justify-content: center;
      align-items: center;
      gap: 25px;
    }
    @include mobile {
      gap: 15px 5px;
    }
  }
}
