.nav {
  &__list {
    display: flex;
    align-items: center;
    gap: 40px;
  }
  &__link {
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 160%;
    color: var(--color-mako);
    transition: color 0.4s;
    &:hover {
      color: var(--color-primary);
    }
  }
}
