.clients {
  padding: 60px;
  @include tablet {
    padding: 50px 0 25px 0;
  }
  &__title {
    margin-bottom: 60px;
  }
  &__container {
    display: flex;
    justify-content: space-between;
    gap: 35px;
    @include tablet {
      flex-direction: column;
    }
  }
  &__content {
    align-self: flex-start;
    position: relative;
    @include tablet {
      align-self: auto;
    }
  }

  &__slider {
    max-width: 600px;
    position: relative;
    @include tablet {
      max-width: none;
    }
  }

  &__slider-avatar {
    max-width: 72px;
    max-height: 72px;
    border-radius: 50%;
    margin-bottom: 25px;
  }

  &__slider-text {
    margin: 0;
    color: var(--color-mako);
    margin-bottom: 25px;
  }

  &__slider-name {
    display: block;
    font-weight: 700;
    font-size: 16px;
    line-height: 160%;
    color: var(--color-black);
    padding-right: 130px;
    margin-bottom: 5px;
  }

  &__slider-pos {
    display: block;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    padding-right: 130px;
    color: var(--color-gray);
  }

  &__nav {
    display: flex;
    gap: 20px;
    position: absolute;
    z-index: 10;
    right: 0;
    bottom: -10px;
  }

  &__nav-next {
    transform: rotate(-180deg);
  }
  &__img {
    img {
      vertical-align: bottom;
      height: 100%;
    }
    @include tablet {
      width: 100%;
      text-align: center;
    }
  }
}
