.know-form {
  transform: translateY(100px);
  padding: 48px 60px;
  border-radius: 4px;
  background-color: var(--color-white);
  box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08),
    0px 30px 24px -10px rgba(154, 156, 165, 0.05),
    0px 12px 10px -6px rgba(154, 156, 165, 0.04),
    0px 4px 4px -4px rgba(30, 33, 44, 0.03);
  @include tablet {
    transform: translate(0);
    padding: 40px 20px;
  }
  &__wrapper {
    display: flex;
    align-items: flex-end;
    gap: 24px;
    @include tablet {
      flex-wrap: wrap;
      justify-content: center;
    }
  }
  &__title {
    margin: 0;
    font-style: normal;
    font-weight: 700;
    font-size: 28px;
    line-height: 150%;
    text-align: center;
    color: var(--color-black);
    margin-bottom: 32px;
    @include tablet {
      font-size: 20px;
    }
  }
  &__label {
    display: flex;
    flex-direction: column;
    width: 260px;
    &--big {
      width: 415px;
    }
    @include small-tablet {
      width: 100%;
    }
  }

  &__caption {
    margin-bottom: 8px;
  }

  &__input {
    flex: 1 0 auto;
  }
}
