.we-offer {
  padding: 120px 0 60px 0;
  &__container {
    display: flex;
    gap: 40px;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  &__img {
    max-width: 705px;
    @include tablet {
      max-width: none;
      width: 100%;
    }
    img {
      width: 100%;
    }
  }

  &__content {
    max-width: 420px;
    @include tablet {
      max-width: none;
    }
  }

  &__title {
    margin-bottom: 60px;
    @include tablet {
      text-align: center;
    }
  }
  &__list {
    display: flex;
    flex-direction: column;
    gap: 25px;
  }
  &__accordion {
    &.open {
      transition: 0.4s ease-out;
      .accordion__icon {
        &::before {
          transform: rotate(0deg);
        }
      }
      .accordion__content {
        opacity: 1;
        overflow: visible;
        transition: 0.4s ease-out;
      }
    }
  }
}
.accordion {
  &__control {
    text-align: left;
    position: relative;
    max-width: 375px;
    padding-left: 60px;
    margin-bottom: 12px;
    @include tablet {
      max-width: none;
    }
  }

  &__title {
    font-style: normal;
    font-weight: 700;
    font-size: 28px;
    line-height: 150%;
    color: var(--color-black);
  }

  &__icon {
    position: absolute;
    left: 0;
    top: 10px;
    display: block;
    width: 22px;
    height: 22px;
    &::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      width: 100%;
      height: 2px;
      border-radius: 100px;
      background-color: var(--color-primary);
      transition: 0.4s;
    }
    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%) rotate(90deg);
      width: 100%;
      height: 2px;
      background-color: var(--color-primary);
      border-radius: 100px;
      transition: 0.4s;
    }
  }

  &__content {
    max-height: 0;
    opacity: 0;
    overflow: hidden;
    will-change: max-height;
    padding-left: 60px;
    transition: 0.4s ease-out;
    p {
      margin: 0;
      font-weight: 400;
      font-size: 16px;
      line-height: 160%;
      color: var(--color-gray);
    }
  }
}
