.mission {
  padding: 120px 0 60px 0;
  @include tablet {
    padding: 50px 0 25px 0;
  }
  &__title {
    text-align: center;
    margin-bottom: 25px;
  }

  &__sub-title {
    text-align: center;
    margin-bottom: 60px;
  }

  &__content {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 30px;
    margin-bottom: 20px;
    @include tablet {
      grid-template-columns: repeat(2, 285px);
      justify-content: center;
      gap: 50px;
    }
  }

  &__item {
    flex: 0 1 285px;
    background-position: top center;
    background-size: 48px;
    background-repeat: no-repeat;
    padding-top: 75px;
    text-align: center;
    &--quality {
      background-image: url(../img/icon/like.svg);
    }
    &--safety {
      background-image: url(../img/icon/hand.svg);
    }
    &--comfort {
      background-image: url(../img/icon/slippers.svg);
    }
  }

  &__item-title {
    margin: 0;
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    margin-bottom: 8px;
  }
  &__item-text {
    color: var(--color-gray);
  }
}
