.slider-controll {
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px;
  border-radius: 50%;
  transition: 0.5s;
  svg {
    width: 18px;
    height: 11px;
    fill: var(--color-black);
  }
  &:hover {
    background-color: var(--color-primary);
    svg {
      fill: var(--color-white);
    }
  }
}
