.benefits {
  background-color: var(--color-black);
  padding: 80px 0 125px 0;
  position: relative;
  &__title {
    text-align: center;
    color: var(--color-white);
    margin-bottom: 25px;
  }

  &__sub-title {
    text-align: center;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    color: var(--color-white);
    opacity: 0.6;
    margin-bottom: 60px;
  }

  &__list {
    display: flex;
    justify-content: space-between;
    gap: 115px;
    @include tablet {
      flex-direction: column;
      align-items: center;
    }
  }

  &__item {
    max-width: 285px;
    text-align: center;
    padding-top: 65px;
    position: relative;
    &--contract {
      background-image: url(../img/icon/contract.svg);
      background-position: top center;
      background-repeat: no-repeat;
      background-size: 48px;
    }
    &--helmet {
      background-image: url(../img/icon/helmet.svg);
      background-position: top center;
      background-repeat: no-repeat;
      background-size: 48px;
    }
    &--camera {
      background-image: url(../img/icon/camera.svg);
      background-position: top center;
      background-repeat: no-repeat;
      background-size: 48px;
    }
    &:not(:last-child) {
      &::after {
        content: "";
        position: absolute;
        right: -94px;
        top: 0;
        background-image: url(../img/divider.svg);
        height: 180px;
        width: 1px;
        @include tablet {
          top: auto;
          bottom: -130px;
          left: 50%;
          transform: rotate(90deg);
        }
      }
    }
  }

  &__info {
    margin: 0;
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    color: var(--color-white);
    margin-bottom: 8px;
  }

  &__descr {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    color: var(--color-white);
    opacity: 0.6;
  }
  &__btn {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%) translateY(25px);
  }
}
