.about {
  padding: 120px 0 60px 0;
  @include tablet {
    padding: 50px 0 25px 0;
  }
  &__title {
    text-align: center;
    margin-bottom: 25px;
  }

  &__under-title {
    text-align: center;
    margin-bottom: 60px;
  }

  &__video {
    position: relative;
    border-radius: 4px;
  }
  &__video-play {
    cursor: pointer;
    position: absolute;
    z-index: 10;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: var(--color-primary);
    width: 80px;
    height: 80px;
    border-radius: 50%;
    padding: 27px;
    svg {
      width: 26px;
      height: 26px;
      fill: var(--color-white);
    }
  }

  &__video-mute {
    cursor: pointer;
    position: absolute;
    z-index: 10;
    bottom: 32px;
    left: 32px;
    background-color: var(--color-white);
    opacity: 0.12;
    width: 40px;
    height: 40px;
    padding: 10px;
    border-radius: 50%;
    svg {
      width: 20px;
      height: 20px;
    }
  }

  &__video-item {
    border-radius: 4px;
    width: 100%;
    height: 500px;
    object-fit: cover;
    @include mobile {
      height: 350px;
    }
  }
}
