.form-btn {
  cursor: pointer;
  padding: 0 32px;
  background-color: var(--color-primary);
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 44px;
  border-radius: 4px;
  text-transform: uppercase;
  color: var(--color-white);
  @include mobile {
    font-size: 11px;
    padding: 0 20px;
  }
}
.btn {
  cursor: pointer;
  padding: 0 40px;
  background-color: var(--color-primary);
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 52px;
  border-radius: 4px;
  text-transform: uppercase;
  color: var(--color-white);
  @include mobile {
    font-size: 11px;
    padding: 0 20px;
  }
}
.second-btn {
  cursor: pointer;
  padding: 0 32px;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 44px;
  color: var(--color-primary);
  background-color: transparent;
  border-radius: 4px;
  border: 1px solid var(--color-primary);
  @include mobile {
    font-size: 11px;
    padding: 0 20px;
  }
}
