.services {
  padding: 212px 0 85px 0;
  @include tablet {
    padding: 50px 0 25px 0;
  }
  &__title {
    text-align: center;
    margin-bottom: 25px;
  }
  &__sub-title {
    text-align: center;
    margin-bottom: 60px;
  }
  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 30px;
    margin-bottom: 85px;
    @include tablet {
      grid-template-columns: repeat(2, 1fr);
    }
    @include mobile {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  &__item {
    position: relative;
    padding: 90px 25px;
    border: 1px solid #e5e8ed;
    box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08),
      0px 30px 24px -10px rgba(154, 156, 165, 0.05),
      0px 12px 10px -6px rgba(154, 156, 165, 0.04),
      0px 4px 4px -4px rgba(30, 33, 44, 0.03);
    border-radius: 4px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    &--plan {
      background-image: url(../img/services-img.jpg);
    }
    &--design {
      background-image: url(../img/services-img.jpg);
    }
    &--repairs {
      background-image: url(../img/services-img.jpg);
    }
    &--construction {
      background-image: url(../img/services-img.jpg);
    }
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      z-index: 2;
      border-radius: 4px;
      background-color: rgb(#1e212c, 0.6);
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
      transition: 0.4s;
    }
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      z-index: 3;
      border-radius: 4px;
      background-color: var(--color-white);
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
      transition: 0.4s;
    }
    &:hover {
      &::after {
        opacity: 0;
      }
      .services__item-content {
        svg {
          fill: var(--color-white);
        }
      }
      .services__item-text {
        color: var(--color-white);
      }
    }
  }
  &__item-content {
    position: relative;
    z-index: 5;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    svg {
      width: 52px;
      height: 48px;
      fill: var(--color-primary);
      margin-bottom: 25px;
    }
  }
  &__item-text {
    margin: 0;
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    color: var(--color-black);
  }
}
