.contacts {
  padding: 80px 0;
  background-image: url(../img/contacts.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  @include tablet {
    padding: 50px 0 25px 0;
  }
  &__container {
    display: flex;
    justify-content: flex-end;
  }
  &__content {
    max-width: 495px;
    padding: 48px 40px;
    display: flex;
    flex-direction: column;
    border: 1px solid #e5e8ed;
    background-color: var(--color-white);
    box-shadow: 0px 60px 80px -20px rgba(30, 33, 44, 0.16),
      0px 26px 24px -10px rgba(30, 33, 44, 0.1),
      0px 12px 10px -6px rgba(30, 33, 44, 0.08),
      0px 4px 4px -4px rgba(30, 33, 44, 0.05);
    border-radius: 4px;
  }
}
.form__contacts {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  &-title {
    margin: 0;
    font-style: normal;
    font-weight: 700;
    font-size: 28px;
    line-height: 150%;
    margin-bottom: 25px;
    @include mobile {
      font-size: 22px;
    }
  }
  &-label {
    display: flex;
    flex-direction: column;
    gap: 8px;
    margin-bottom: 20px;
    width: 100%;
  }
  &-text {
    width: 100%;
    padding: 11px 16px;
    resize: none;
    border: 1px solid #d7dadd;
    height: 66px;
    margin-bottom: 20px;
  }

  &-check {
    display: flex;
    align-items: flex-start;
    gap: 12px;
    margin-bottom: 25px;
    input {
      transform: translateY(5px);
    }
  }
  &-info {
    margin: 0;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: var(--color-mako);
  }
}
