.services-content {
  padding: 120px 0 180px 0;
  @include tablet {
    padding: 50px 0 25px 0;
  }
  &__list {
    display: flex;
    flex-direction: column;
    gap: 120px;
    @include small-tablet {
      gap: 80px;
    }
  }
  &__item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 25px;
    &:nth-child(2n) {
      flex-direction: row-reverse;
      @include tablet {
        flex-direction: column;
      }
    }
    @include tablet {
      flex-direction: column;
    }
  }

  &__img {
    flex-shrink: 0;
    width: 575px;

    img {
      width: 100%;
      max-height: 420px;
    }
    @include tablet {
      width: 100%;
      flex-shrink: 1;
    }
  }

  &__text {
    max-width: 575px;
    @include tablet {
      max-width: none;
    }
  }
}

.services-info {
  &__title {
    margin: 0;
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 150%;
    color: var(--color-black);
    margin-bottom: 25px;
  }

  &__descr {
    margin: 0;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    color: var(--color-gray);
    margin-bottom: 45px;
  }

  &__btn {
    display: inline-block;
  }
}
