.pricing {
  padding: 80px 0;
  &__title {
    text-align: center;
    color: var(--color-black);
    margin-bottom: 25px;
  }
  &__sub-title {
    margin: 0;
    text-align: center;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    color: var(--color-gray);
    margin-bottom: 60px;
  }
}
.table-wrapper {
  position: relative;
  z-index: 5;
  overflow: hidden;
  border: 1px solid #e5e8ed;
  border-collapse: collapse;
  border-radius: 4px;
  width: 100%;
  @include small-tablet {
    overflow-x: auto;
  }
}

.table {
  border-collapse: collapse;
  width: 100%;

  &__heading {
    margin-bottom: 2px;
    font-weight: 700;
    font-size: 24px;
    line-height: 150%;
    text-align: center;
    text-transform: uppercase;
  }

  &__per {
    font-weight: 800;
    font-size: 18px;
    line-height: 150%;
    text-align: center;
    color: var(--color-mako);
  }

  &__th {
    padding-top: 31px;
    padding-right: 20px;
    padding-bottom: 24px;

    &:first-child {
      vertical-align: bottom;
      padding-left: 24px;
      font-weight: 700;
      font-size: 18px;
      line-height: 150%;
      text-align: left;
    }
  }

  &__body {
    .table__row {
      &:nth-child(odd) {
        background-color: #f4f5f7;
      }

      &:last-child {
        .table__col {
          padding-top: 30px;
          padding-bottom: 30px;
        }
      }
    }
  }

  &__col {
    vertical-align: middle;
    padding: 16px 0;
    padding-right: 10px;
    font-size: 16px;
    line-height: 160%;
    text-align: center;
    color: var(--color-gray);

    .btn {
      padding: 0 20px;
      font-weight: 700;
      font-size: 12px;
      line-height: 36px;
      letter-spacing: 0.5px;
      text-transform: uppercase;

      &:focus {
        outline-offset: 2px;
      }

      @include tablet {
        padding: 0 10px;
      }
    }

    &:first-child {
      padding-left: 24px;
      text-align: left;
    }
  }

  &__check {
    display: inline-block;
    vertical-align: middle;
    width: 24px;
    height: 24px;
    font-size: 0;
    background-image: url("../img/table-check.svg");
    background-position: center;
    background-size: 14px;
    background-repeat: no-repeat;
  }

  &__text {
    font-size: 16px;
    line-height: 160%;
    text-align: center;
    color: var(--color-gray);
  }

  @include small-tablet {
    width: 1200px;
  }
}
