.recent-article {
  border-radius: 4px;
  overflow: hidden;
  transition: 0.4s;
  img {
    width: 100%;
  }
  &__content {
    padding: 24px 25px;
    &--small {
      padding: 16px 24px 24px;
    }
  }

  &__title {
    margin: 0;
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    color: var(--color-black);
    margin-bottom: 12px;
    transition: 0.4s;
    @include mobile {
      font-size: 16px;
    }
  }

  &__info {
    display: flex;
    align-items: center;
    gap: 25px;
    @include mobile {
      flex-wrap: wrap;
      gap: 10px;
    }
  }

  &__item {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: var(--color-gray);
    position: relative;
    &:not(:last-child) {
      &::after {
        content: "";
        position: absolute;
        top: 5px;
        right: -12px;
        width: 1px;
        height: 10px;
        background-color: var(--color-gray);
        @include mobile {
          top: 5px;
          right: -6px;
        }
      }
    }
  }

  &__descr {
    margin-top: 16px;
    color: var(--color-mako);
  }
  &:hover {
    background-color: var(--color-white);
    box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.16),
      0px 30px 24px -10px rgba(154, 156, 165, 0.1),
      0px 12px 10px -6px rgba(154, 156, 165, 0.08),
      0px 4px 4px -4px rgba(30, 33, 44, 0.05);
    .recent-article__title {
      color: var(--color-primary);
    }
  }
}
.recent-article--comments {
  background-image: url(../img/comments.svg);
  background-size: 16px;
  background-position: left center;
  background-repeat: no-repeat;
  padding-left: 25px;
}
