.how-we-do {
  padding: 80px 0 120px 0;
  &__title {
    margin-bottom: 60px;
    @include tablet {
      text-align: center;
    }
  }
  &__list {
    display: flex;
    gap: 30px;
    counter-reset: section;
    flex-wrap: wrap;
    @include tablet {
      justify-content: center;
      align-items: center;
    }
  }

  &__item {
    max-width: 285px;
    position: relative;
    &::before {
      counter-increment: section;
      content: "0" counter(section);
      display: block;
      width: 84px;
      height: 84px;
      font-style: normal;
      font-weight: 700;
      font-size: 46px;
      line-height: 130%;
      color: #d7dadd;
      border-radius: 50%;
      margin-bottom: 4px;
      transition: 0.3s;
      @include tablet {
        margin: 0 auto;
      }
    }
    &:hover {
      &::before {
        color: var(--color-primary);
        padding: 12px 15px;
        background-color: rgb(#ff5a30, 0.12);
      }
    }
    @include tablet {
      text-align: center;
      max-width: none;
    }
  }

  &__descr {
    margin: 0;
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    margin-bottom: 8px;
  }

  &__text {
    margin: 0;
  }
}
