.recent-news {
  padding: 60px 0;
  @include tablet {
    padding: 50px 0 25px 0;
  }
  &__title {
    text-align: center;
    margin-bottom: 60px;
  }
  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(2, 255px);
    gap: 30px;
    margin-bottom: 85px;
    @include tablet {
      display: flex;
      flex-direction: column;
    }
    @include mobile {
      gap: 20px;
    }
  }
  &__item {
    grid-column: 5 span;
    &:first-child {
      grid-column: 7 span;
      grid-row: 2 span;
    }
  }
}
