.portfolio {
  padding: 120px 0 60px 0;
  @include tablet {
    padding: 50px 0 25px 0;
  }
  &__head {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    gap: 35px;
    margin-bottom: 60px;
    flex-wrap: wrap;
  }

  &__title {
    max-width: 705px;
  }

  &__nav {
    display: flex;
  }

  &__prev {
    transform: rotate(180deg);
  }
}
