.hero {
  &__container {
    position: relative;
  }
  &__content {
    max-width: 577px;
  }
  &__slide {
    background-image: url(../img/hero-img.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    padding: 220px 0 355px 0;
    @include tablet {
      padding: 80px 0 100px 0;
    }
  }
  &__content-title {
    margin: 0;
    font-style: normal;
    font-weight: 700;
    font-size: 72px;
    line-height: 130%;
    margin-bottom: 24px;
    color: var(--color-white);
    @include small-tablet {
      font-size: 47px;
    }
    @include small-tablet {
      font-size: 35px;
    }
  }
  &__nav {
    @include big-desktop {
      display: none;
    }
  }
  &__content--primary {
    color: var(--color-primary);
  }

  &__content-text {
    margin: 0;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 150%;
    color: var(--color-white);
    margin-bottom: 60px;
    @include small-tablet {
      font-size: 15px;
    }
  }

  &__btns {
    display: flex;
    gap: 25px;
    @include small-tablet {
      flex-direction: column;
    }
  }

  &__btn {
    padding: 0 40px;
    border: 1px solid var(--color-white);
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 52px;
    letter-spacing: 0.5px;
    border-radius: 4px;
    text-transform: uppercase;
    color: var(--color-white);
  }

  &__btn--primary {
    border-color: transparent;
    background-color: var(--color-primary);
  }

  &__nav-item {
    width: 48px;
    height: 48px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--color-white);
    border-radius: 50%;
    opacity: 0.5;
    transition: opacity 0.5s;
    svg {
      width: 18px;
      height: 11px;
    }
    &:hover {
      opacity: 1;
    }
    &.swiper-button-disabled {
      &:hover {
        opacity: 0.5;
      }
    }
  }
  &__pag {
    counter-reset: pag;
    display: flex;
    flex-wrap: wrap;
    padding-left: max(15px, calc((100% - 1245px) / 2));
    bottom: 120px !important;
    .swiper-pagination-bullet {
      display: flex;
      flex-direction: column;
      border-radius: 0;
      width: 178px;
      height: auto;
      text-align: left;
      background-color: transparent;
      opacity: 1;
      &::before {
        counter-increment: pag;
        content: "0" counter(pag);
        font-style: normal;
        font-weight: 700;
        font-size: 28px;
        line-height: 150%;
        color: var(--color-white);
        opacity: 0.6;
        transition: opacity 0.3s ease-in-out;
      }
      &::after {
        content: "";
        width: 100%;
        height: 2px;
      }
      &.swiper-pagination-bullet-active {
        .hero__bar {
          &::after {
            transition: transform var(--hero-slider-speed) ease-in-out;
            transform: scaleX(1);
          }
        }
        &::before {
          opacity: 1;
        }
      }
    }
    @include tablet {
      bottom: 40px !important;
    }
    @include mobile {
      display: none;
    }
  }
  &__bar {
    position: relative;
    width: 100%;
    height: 2px;
    background-color: rgb(#fff, 0.3);
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: var(--color-white);
      transform: scaleX(0);
      transform-origin: left;
    }
  }
}
.hero__nav--prev {
  position: absolute;
  top: 50%;
  left: 32px;
  transform: translateX(-50%);
  z-index: 50;
}
.hero__nav--next {
  position: absolute;
  top: 50%;
  right: 32px;
  transform: translateX(-50%) rotate(180deg);
  z-index: 50;
}
