.section-under {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 40px;
  &__text {
    margin: 0;
    font-style: normal;
    font-weight: 700;
    font-size: 28px;
    line-height: 150%;
    text-align: center;
    color: var(--color-black);
  }
  @include mobile {
    flex-direction: column;
  }
}
