.breadcrumbs {
  display: flex;
  gap: 22px;
  &__item {
    position: relative;
    &:not(:last-child) {
      &::after {
        content: "/";
        position: absolute;
        top: 0;
        right: -15px;
        color: var(--color-manatee);
      }
    }
  }
  &__link {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: var(--color-mako);
  }

  &__link--active {
    color: var(--color-manatee);
  }
}
