.portfolio__slider {
  margin-bottom: 80px;
  padding-bottom: 50px;
  .swiper-slide {
    position: relative;
    transition: 0.4s;
    border-radius: 4px;
    &:hover {
      background-color: var(--color-white);
      box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08),
        0px 30px 24px -10px rgba(154, 156, 165, 0.05),
        0px 12px 10px -6px rgba(154, 156, 165, 0.04),
        0px 4px 4px -4px rgba(30, 33, 44, 0.03);

      .portfolio__slider-btn {
        transform: translateY(0);
        opacity: 1;
        display: inline-block;
      }
    }
    @include small-tablet {
      background-color: var(--color-white);
      box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08),
        0px 30px 24px -10px rgba(154, 156, 165, 0.05),
        0px 12px 10px -6px rgba(154, 156, 165, 0.04),
        0px 4px 4px -4px rgba(30, 33, 44, 0.03);
    }
  }
  @include small-tablet {
    margin-bottom: 50px;
    padding-bottom: 0;
  }
  &-content {
    text-align: center;
    padding: 16px 20px 24px;
    transition: 0.5s;
  }
  &-img {
    vertical-align: bottom;
    width: 100%;
    min-height: 345px;
  }
  &-title {
    margin: 0;
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    color: var(--color-black);
    margin-bottom: 4px;
  }
  &-text {
    margin: 0;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: var(--color-gray);
  }

  &-btn {
    margin-top: 24px;
    opacity: 0;
    @include small-tablet {
      opacity: 1;
    }
  }
}
